import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import uniNumberBox from '../../components/uni-number-box/uni-number-box.vue'
import api from '@/api/api.js'
import cartList from '../../json.js'
export default {
	data() {
		return {
			showCart:false,
			list: cartList.cartList,
			// list:'',
			totalPrice: 0,
			IsallCheck:true
		};
	},
	onShow() {
		let self = this;
		uni.getStorage({
			key:'userInfo',
			success:function(){
				self.showCart = true;
				
			}
		});
		
	},
	onLoad() {
		
		this.list.forEach((item)=>{
			item.checked =true
		})
		this.calcTotal()
	},
	components:{
		uniNavBar,
		uniNumberBox
	},
	watch:{
		// list:function(){
		// 	console.log('change')
		// 	this.list.forEach(item =>{
		// 		if(item.checked){
		// 			this.totalPrice += item.price
		// 		}
		// 	})
		// }
	},
	methods:{
		numberChange(data){
			this.list[data.index].number = data.number
			this.calcTotal()
			console.log(data)
		},
		calcTotal(){
			let total = 0
			this.list.forEach(item =>{
				if(item.checked){
					total += item.price * (item.number>=item.stock?item.stock:item.number)
					// console.log(total)
				}
			})
			this.totalPrice = total.toFixed(2)
		},
		allCheck(){
			const checked = !this.IsallCheck
			let cartList = this.list.map(item=>{
				item.checked = checked;
				return item;
			});
			this.IsallCheck = checked
			this.list = cartList;
			this.calcTotal()
		},
		delHandle(index){
			if(this.list.length -1 === 0){
				this.showCart = false
				return
			}
			const items = this.list;
			items.splice(index,1);
			this.calcTotal()
		},
		isChecked(index){
			let checked = true;
			this.list[index].checked = !this.list[index].checked;
			this.list.forEach((item) =>{
				if( item.checked === true){
					return
				}else{
					checked = false
				}
			})
			// this.list.find((item,id,arr)=>{
			// 	if( item.checked === false){
					
			// 		return checked = false
			// 	}
			// })
			this.IsallCheck = checked
			this.calcTotal();
			// this.$forceUpdate()
		}
	},
	
}
